<template>
    <div class="demo-treelist">
        <ui-list>
            <ui-listitem to="/"></ui-listitem>
            <ui-listitem to="/icon"></ui-listitem>
        </ui-list>
        <ui-divider></ui-divider>
        <ui-list>
            <ui-listitem :children="treeRoutes">
                <i class="icon-sitemap"></i> 
                <template slot="children"
                          slot-scope="{ value }">
                    <i v-if="value.icon"
                       :class="`icon-${value.icon}`"></i> {{value.text}}
                </template>
            </ui-listitem>
        </ui-list>
    </div>
</template>
<script>
export default {
    data () {
        return {
            treeRoutes: [{
                id: 111,
                text: '',
                icon: 'code',
                children: [{
                    id: 1111,
                    text: ''
                }, {
                    id: 2222,
                    text: ''
                }, {
                    id: 3333,
                    text: ''
                }, {
                    id: 4444,
                    text: ''
                }]
            }, {
                id: 222,
                text: '',
                icon: 'tasks',
            }, {
                id: 333,
                text: '',
                icon: 'user-circle'
            }]
        }
    }
}
</script>
<style>
.demo-treelist {
    max-width: 300px;
    border: 1px solid #ddd;
}
</style>



