<template>
    <div>
        <button v-popover:full>1</button>
        <ui-popover fullHeight
                    name="full">
            <ul>
                <li v-for="n in 100"> {{n}} </li>
            </ul>
        </ui-popover>
        <button v-popover:full2>2</button>
        <ui-popover fullHeight
                    name="full2"
                    placement="right">
            <ul>
                <li v-for="n in 100"> {{n}} </li>
            </ul>
        </ui-popover>
    </div>
</template>
<script>
export default {
}
</script>
<style scoped>
li {
    padding: 8px 10px;
    text-align: center
}
</style>



